.menu {
    display: flex;
    align-items: center;
    gap: 8px;

    a {
        padding: 8px;
        line-height: 0;
        opacity: 0.7;
        transition: opacity 0.3s ease;

        &:hover {
            opacity: 1;
        }

        &:focus {
            opacity: 1;
        }
    }
}
