.settings {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -20%);
    max-width: 500px;
    width: 100%;

    padding: 40px;
    background-color: var(--bg);

    visibility: hidden;
    transition:
        transform 0.24s ease-in-out,
        opacity 0.24s ease-in-out,
        visibility 0.24s ease-in-out;
    opacity: 0;
    pointer-events: none;

    z-index: 9;

    .settings__item {
        display: grid;
        grid-template-columns: auto minmax(140px, 1fr) auto;
        gap: 8px;
        margin-bottom: 15px;

        label {
            grid-column: 3 span;
            justify-self: center;
            font-size: 14px;
            font-style: italic;
            font-weight: 300;
        }

        button {
            padding: 8px;
            line-height: 0;
            border: 1px solid var(--base07);
            border-radius: 5px;
            background-color: var(--base00);
            color: var(--base07);
            cursor: pointer;
        }

        input {
            padding: 8px;
            border: 1px solid var(--base07);
            border-radius: 5px;
            background-color: var(--base00);
            color: var(--base07);
            text-align: center;
            font-size: 18px;
        }
    }

    &__close-btn {
        position: absolute;
        top: -10px;
        right: 20px;
        padding: 8px 12px;
        background-color: transparent;
        border: none;
        cursor: pointer;
        opacity: 0.7;
        transition: opacity 0.3s ease-in-out;

        &:hover {
            opacity: 1;
        }

        &:focus {
            opacity: 1;
        }
    }
}

.settings.open {
    transform: translate(-50%, -50%);
    visibility: visible;
    pointer-events: all;
    opacity: 1;
}
