.form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 35px 15px;
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: var(--base07);

    @media (max-width: 400px) {
        gap: 30px 10px;
    }

    &__label {
        display: grid;
        gap: 5px;

        input {
            padding: 4px 5px;
            border: 1px solid #faeedd;
            background-color: #faeedd;

            @media (max-width: 400px) {
                min-width: 140px;
            }
        }
    }

    &__info {
        display: grid;
        gap: 5px;

        span {
            padding: 4px 5px;
            background-color: #232323;
        }
    }

    &__wrap {
        grid-column: span 2;
        text-align: center;
    }

    button {
        padding: 12px;
        min-width: 145px;
        font-family: "Roboto", sans-serif;
        font-size: 16px;
        border: 1px solid var(--base03);
        border-radius: 5px;
        color: #fff;
        background-color: transparent;
        transition: border-color 0.3s ease-in-out;
        cursor: pointer;

        &:hover {
            border-color: var(--base07);
        }
    }
}
