.crops {
    padding-top: 60px;
    padding-bottom: 30px;

    &__wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 35px;
    }

    &__select {
        display: flex;
        justify-content: end;
    }

    @media (max-width: 640px) {
        padding-top: 30px;

        &__wrapper {
            flex-direction: column-reverse;
            align-items: end;
            gap: 20px;
        }
    }
}
