:root {
    --bg: #333333;
    --base00: #383838;
    --base01: #434343;
    --base02: #515151;
    --base03: #666;
    --base04: #818181;
    --base05: #a4a4a4;
    --base06: #c8c8c8;
    --base07: #e3e3e3;
    --danger: #dc143c;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--bg);
}

a {
    text-decoration: none;
}

.App {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    min-height: 100vh;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    color: #fff;
}

.App.active {
    background-color: var(--danger);
}

.container {
    max-width: 680px;
    width: 100%;
    margin: 0 auto;
}
