.modal {
    margin-bottom: 20px;
    overflow: hidden;
}

.modal__content {
    display: none;

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 100px 15px 40px 15px;

    grid-template-columns: repeat(4, max-content);
    justify-content: center;
    align-content: center;
    gap: 10px;

    background-color: var(--bg);

    @media (max-width: 940px) {
        grid-template-columns: repeat(3, max-content);
    }
    @media (max-width: 680px) {
        grid-template-columns: repeat(2, max-content);
        padding: 20px 15px 20px 15px;
    }
    @media (max-width: 400px) {
        grid-template-columns: repeat(2, max-1fr);
    }
}

.modal__content.open {
    display: grid;
}

.modal__btn {
    display: flex;
    align-items: center;
    gap: 8px;

    color: var(--base07);
    background-color: var(--base00);
    padding: 10px 12px;
    border: none;
    transition: background-color 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
        background-color: var(--base01);
    }

    padding: 10px 8px;

    @media (max-width: 400px) {
        padding: 10px 8px;
    }
    @media (max-width: 370px) {
        flex-direction: column;
    }
}

.modal-open {
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;

    font-family: "Roboto", sans-serif;
    font-size: 16px;

    border: none;
    color: var(--base07);
    background-color: var(--bg);
    cursor: pointer;

    transition: background-color 0.3s ease-in-out;

    &:hover {
        background-color: #232323;
    }
}

.modal-close {
    position: relative;
    left: 10px;
    grid-column: 4 span;
    justify-self: end;
    width: 40px;
    height: 40px;
    margin-bottom: 10px;

    border: none;
    background-color: var(--bg);
    opacity: 0.7;
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
        opacity: 1;
    }

    &:focus {
        opacity: 1;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @media (max-width: 940px) {
        grid-column: 3 span;
    }
    @media (max-width: 680px) {
        grid-column: 2 span;
    }
}
