.timer {
    display: flex;
    flex-direction: column;

    position: relative;
    top: -40px;

    &__top {
        display: flex;
        align-items: center;
    }

    &__info-btn {
        background-color: transparent;
        border: none;
        padding: 10px;
        cursor: pointer;
        opacity: 0.7;
        transition: opacity 0.3s ease-in-out;

        &:hover {
            opacity: 1;
        }

        &:focus {
            opacity: 1;
        }
    }

    &__title {
        font-family: "Roboto", sans-serif;
        font-size: 14px;
        padding-left: 8px;
        font-weight: 500;
    }

    &__content {
        display: flex;
        gap: 15px;
        font-family: "Roboto", sans-serif;
        font-size: clamp(60px, 7.5vw, 104px);
        line-height: 100%;
        font-weight: 500;

        span {
            margin-top: -8px;
        }
    }

    &__menu {
        display: flex;
        justify-content: flex-end;
        gap: 15px;
        align-self: end;
        margin-bottom: 40px;
    }
}
