.back-btn {
    --padding: 10px;

    display: flex;
    gap: 10px;
    margin-right: calc((var(--padding)) * -1);
    padding: var(--padding);

    color: #fff;
    opacity: 0.7;
    transition: opacity 0.3s ease;

    &:hover {
        opacity: 1;
    }

    img {
        width: 18px;
    }
}
