.crops-item {
    display: grid;
    grid-template-columns: 32px 120px 60px 1fr 1fr;
    justify-content: end;
    align-items: center;
    gap: 15px;
    padding: 10px 12px;
    margin-bottom: 5px;
    background-color: var(--base00);

    &__name {
        font-size: 14px;
    }

    &__level {
        font-family: "Roboto", sans-serif;
        font-size: 11px;
    }

    &__bounty {
        display: flex;
        align-items: center;
        gap: 8px;
        font-family: "Roboto", sans-serif;
        font-size: 12px;
        color: var(--base06);
    }

    @media (max-width: 640px) {
        grid-template-columns: 32px 1fr 40px 1fr 1fr;
        gap: 10px;

        &__name {
            font-size: 13px;
        }

        &__bounty {
            flex-direction: column;
            text-align: center;
        }
    }
}
